import React from 'react';
import './DocumentosPage.css';

// Documentos reales en la carpeta Documentation
const documentos = [
  {
    nombre: 'Infografa Conceptos Bsicos Huella de Carbono',
    resumen: 'Resumen visual de los conceptos fundamentales sobre la huella de carbono y su importancia.',
    archivo: '/Documentation/1. Infografa Conceptos Bsicos Huella de Carbono.pdf',
  },
  {
    nombre: 'Infografa Alcances y Limites de la Medicin de Huella de Carbono',
    resumen: 'Explicacin de los diferentes alcances y lmites en la medicin de la huella de carbono.',
    archivo: '/Documentation/3. Infografa Alcances y Limites de la Medicin de Huella de Carbono.pdf',
  },
  {
    nombre: 'Documento factores de Emisin',
    resumen: 'Documento detallado sobre los factores de emisin utilizados en los clculos de huella.',
    archivo: '/Documentation/5. Documento factores de Emisin.pdf',
  },
  {
    nombre: 'Infografa Acciones para reducir la huella de CO2',
    resumen: 'Acciones recomendadas para reducir la huella de carbono en organizaciones y personas.',
    archivo: '/Documentation/7. Infografa Acciones para reducir la huella de CO2.pdf',
  },
  {
    nombre: 'Infografa Elaboracin de reportes de medicin',
    resumen: 'Gua visual para la elaboracin de reportes de medicin de huella de carbono.',
    archivo: '/Documentation/8. Infografa Elaboracin de reportes de medicin.pdf',
  },
];

const DocumentosPage = () => {
  return (
    <div className="documentos-page">
      <h1 className="documentos-title">Documentos y Recursos</h1>
      <p className="documentos-desc">Accede y descarga documentos clave sobre huella de carbono. Cada recurso incluye un breve resumen para ayudarte a identificar el contenido ms relevante para ti.</p>
      <div className="documentos-list">
        {documentos.map((doc, idx) => (
          <div className="documento-card" key={idx}>
            <div className="documento-info">
              <h2 className="documento-nombre">{doc.nombre}</h2>
              <p className="documento-resumen">{doc.resumen}</p>
            </div>
            <a className="documento-descargar" href={doc.archivo} download>
              Descargar
            </a>
          </div>
        ))}
      </div>
    </div>
  );
};

export default DocumentosPage;
